.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}
.dialog {
    padding: 10px;
}

.dialog img {
    height: 50px;
    border-radius: 30px;
    margin: 5px;
}

.photo {
    width: 50px;
    height: 50px;
    border-radius: 30px;
}
.selectedPage {
    font-weight: bold;
}
.page {
    cursor: pointer;
}
